'use client';

import { useCallback, useState, useEffect, useMemo } from 'react';
import {
  useUser,
  useFirestore,
  useCollection,
  useMemoFirebase,
  useAuth,
  useDoc,
} from '@/firebase';
import {
  addDocumentNonBlocking,
  deleteDocumentNonBlocking,
  setDocumentNonBlocking,
} from '@/firebase/non-blocking-updates';
import { initiateAnonymousSignIn } from '@/firebase/non-blocking-login';
import {
  collection,
  doc,
} from 'firebase/firestore';

import { Calendar as CalendarIcon } from 'lucide-react';
import { format } from 'date-fns';
import { useToast } from '@/hooks/use-toast';
import type { Expense, Income, SavingsGoal } from '@/lib/types';
import SypCalculator from './SypCalculator';
import ExpenseTracker from './ExpenseTracker';
import SettingsForm from './SettingsForm';
import SummaryCards from './SummaryCards';
import { Skeleton } from '@/components/ui/skeleton';
import { Button } from '@/components/ui/button';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { cn } from '@/lib/utils';
import IncomeTracker from './IncomeTracker';

const SpendSmartDashboard = () => {
  const [isClient, setIsClient] = useState(false);
  const { user, isUserLoading } = useUser();
  const auth = useAuth();
  const firestore = useFirestore();
  const { toast } = useToast();

  const [selectedDate, setSelectedDate] = useState(new Date());

  useEffect(() => {
    setIsClient(true);
    if (!isUserLoading && !user) {
      initiateAnonymousSignIn(auth);
    }
  }, [isUserLoading, user, auth]);

  // Firestore data hooks
  const expensesQuery = useMemoFirebase(() => {
    if (!user) return null;
    return collection(firestore, 'users', user.uid, 'expenses');
  }, [firestore, user]);
  const { data: expenses, isLoading: expensesLoading } =
    useCollection<Expense>(expensesQuery);

  const incomesQuery = useMemoFirebase(() => {
    if (!user) return null;
    return collection(firestore, 'users', user.uid, 'incomes');
  }, [firestore, user]);
  const { data: incomes, isLoading: incomesLoading } =
    useCollection<Income>(incomesQuery);

  const savingsGoalDocRef = useMemoFirebase(() => {
    if (!user) return null;
    return doc(firestore, 'users', user.uid, 'savingsGoal', 'main');
  }, [firestore, user]);
  const { data: savingsGoalData, isLoading: savingsGoalLoading } = useDoc<SavingsGoal>(savingsGoalDocRef);
  
  const savingsGoal = useMemo(() => {
    return savingsGoalData || { targetAmount: 500 };
  }, [savingsGoalData]);

  const isLoading =
    isUserLoading ||
    expensesLoading ||
    incomesLoading ||
    savingsGoalLoading ||
    !isClient;

  const handleAddExpense = useCallback(
    (newExpense: Omit<Expense, 'id' | 'userId' | 'date'>) => {
      if (!user || !firestore) return;
      const expenseCol = collection(firestore, 'users', user.uid, 'expenses');
      addDocumentNonBlocking(expenseCol, {
        ...newExpense,
        userId: user.uid,
        date: format(selectedDate, 'yyyy-MM-dd'),
      });
      toast({
        title: 'Expense Added',
        description: `${newExpense.description} has been logged.`,
      });
    },
    [user, firestore, selectedDate, toast]
  );

  const handleRemoveExpense = (expenseId: string) => {
    if (!user || !firestore) return;
    const docRef = doc(firestore, 'users', user.uid, 'expenses', expenseId);
    deleteDocumentNonBlocking(docRef);
    toast({
      variant: 'destructive',
      title: 'Expense Removed',
      description: `Expense has been removed.`,
    });
  };

  const handleAddIncome = useCallback(
    (newIncome: Omit<Income, 'id' | 'userId' | 'date'>) => {
      if (!user || !firestore) return;
      const incomeCol = collection(firestore, 'users', user.uid, 'incomes');
      addDocumentNonBlocking(incomeCol, {
        ...newIncome,
        userId: user.uid,
        date: format(selectedDate, 'yyyy-MM-dd'),
      });
      toast({
        title: 'Income Added',
        description: `${newIncome.description} has been logged.`,
      });
    },
    [user, firestore, selectedDate, toast]
  );

  const handleRemoveIncome = (incomeId: string) => {
    if (!user || !firestore) return;
    const docRef = doc(firestore, 'users', user.uid, 'incomes', incomeId);
    deleteDocumentNonBlocking(docRef);
    toast({
      variant: 'destructive',
      title: 'Income Removed',
      description: `Income has been removed.`,
    });
  };

  const handleUpdateSettings = ({
    targetAmount,
  }: {
    targetAmount: number;
  }) => {
    if (!user || !firestore) return;
    const goalId = 'main';
    const docRef = doc(firestore, 'users', user.uid, 'savingsGoal', goalId);
    setDocumentNonBlocking(docRef, { userId: user.uid, targetAmount }, { merge: true });
    toast({
      title: 'Settings Saved',
      description: 'Your financial settings have been updated.',
    });
  };

  const expensesForSelectedDate = useMemo(() => {
    const dateStr = format(selectedDate, 'yyyy-MM-dd');
    return (expenses || []).filter((exp) => exp.date === dateStr);
  }, [expenses, selectedDate]);

  const incomesForSelectedDate = useMemo(() => {
    const dateStr = format(selectedDate, 'yyyy-MM-dd');
    return (incomes || []).filter((inc) => inc.date === dateStr);
  }, [incomes, selectedDate]);

  const spentOnSelectedDate = useMemo(() => {
    return expensesForSelectedDate.reduce((acc, exp) => acc + exp.amount, 0);
  }, [expensesForSelectedDate]);

  const earnedOnSelectedDate = useMemo(() => {
    return incomesForSelectedDate.reduce((acc, inc) => acc + inc.amount, 0);
  }, [incomesForSelectedDate]);

  const totalIncomeForMonth = useMemo(() => {
    const currentMonth = format(selectedDate, 'yyyy-MM');
    return (incomes || [])
      .filter((inc) => inc.date.startsWith(currentMonth))
      .reduce((acc, inc) => acc + inc.amount, 0);
  }, [incomes, selectedDate]);

  const dailyBudget = useMemo(() => {
    const daysInMonth = new Date(
      selectedDate.getFullYear(),
      selectedDate.getMonth() + 1,
      0
    ).getDate();
    const availableToSpend = totalIncomeForMonth - savingsGoal.targetAmount;
    const dailySpendLimit =
      availableToSpend > 0 ? availableToSpend / daysInMonth : 0;
    return dailySpendLimit;
  }, [totalIncomeForMonth, savingsGoal, selectedDate]);

  const remainingToday = useMemo(() => {
    return earnedOnSelectedDate - spentOnSelectedDate;
  }, [earnedOnSelectedDate, spentOnSelectedDate]);

  if (isLoading) {
    return (
      <div className="flex flex-col gap-6 lg:gap-8">
        <div className="flex justify-between items-center">
            <Skeleton className="h-10 w-64" />
            <Skeleton className="h-10 w-[280px]" />
        </div>
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
        </div>
        <Skeleton className="h-64" />
        <div className="grid md:grid-cols-3 gap-6 lg:gap-8">
          <Skeleton className="h-80" />
          <Skeleton className="h-80" />
          <Skeleton className="h-80" />
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col gap-6 lg:gap-8">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">
          Dashboard for {format(selectedDate, 'PPP')}
        </h2>
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant={'outline'}
              className={cn(
                'w-[280px] justify-start text-left font-normal',
                !selectedDate && 'text-muted-foreground'
              )}
            >
              <CalendarIcon className="mr-2 h-4 w-4" />
              {selectedDate ? format(selectedDate, 'PPP') : <span>Pick a date</span>}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0">
            <Calendar
              mode="single"
              selected={selectedDate}
              onSelect={(date) => date && setSelectedDate(date)}
              initialFocus
            />
          </PopoverContent>
        </Popover>
      </div>

      <SummaryCards
        dailyBudget={dailyBudget}
        spentToday={spentOnSelectedDate}
        remainingToday={remainingToday}
        earnedToday={earnedOnSelectedDate}
      />

      <SypCalculator onAddExpense={handleAddExpense} />

      <div className="grid md:grid-cols-3 gap-6 lg:gap-8">
        <SettingsForm
          savingsGoal={savingsGoal.targetAmount}
          onUpdate={handleUpdateSettings}
        />
        <ExpenseTracker
          expenses={expensesForSelectedDate}
          onAddExpense={handleAddExpense}
          onRemoveExpense={handleRemoveExpense}
        />
        <IncomeTracker
          incomes={incomesForSelectedDate}
          onAddIncome={handleAddIncome}
          onRemoveIncome={handleRemoveIncome}
        />
      </div>
    </div>
  );
};

export default SpendSmartDashboard;
