'use client';

import type { FC } from 'react';
import { zodResolver } from '@hookform/resolvers/zod';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { PiggyBank, Save } from 'lucide-react';
import { useEffect } from 'react';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';

const settingsFormSchema = z.object({
  targetAmount: z.coerce.number().min(0, 'Savings goal cannot be negative.'),
});

type SettingsFormValues = z.infer<typeof settingsFormSchema>;

interface SettingsFormProps {
  savingsGoal: number;
  onUpdate: (values: { targetAmount: number }) => void;
}

const SettingsForm: FC<SettingsFormProps> = ({ savingsGoal, onUpdate }) => {
  const form = useForm<SettingsFormValues>({
    resolver: zodResolver(settingsFormSchema),
    defaultValues: {
      targetAmount: savingsGoal,
    },
  });

  useEffect(() => {
    form.reset({ targetAmount: savingsGoal });
  }, [savingsGoal, form]);

  const onSubmit = (data: SettingsFormValues) => {
    onUpdate(data);
    form.reset(data, { keepDirty: false });
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Financial Settings</CardTitle>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="targetAmount"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="flex items-center gap-2">
                    <PiggyBank className="text-primary" /> Monthly Savings Goal
                  </FormLabel>
                  <FormControl>
                    <Input
                      type="number"
                      step="50"
                      placeholder="e.g. 500"
                      {...field}
                    />
                  </FormControl>
                  <FormDescription>
                    How much you want to save each month.
                  </FormDescription>
                </FormItem>
              )}
            />
            <Button type="submit" disabled={!form.formState.isDirty}>
              <Save className="mr-2" />
              Save Settings
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
};

export default SettingsForm;
